import { useEffect, useState } from 'react'
import HeroInfo from './components/HeroInfo'
import HeroStatsBox from './components/HeroStatsBox/index.js'
import { DfkHeroCard, buildAPIHero, getApiHero, DfkHeroSprite } from './entry'
import { calculateRemainingStamina } from './utils/staminaCalculations'

function App() {
    const [hero, setHero] = useState(null)
    const [heroId, setHeroId] = useState('34')
    const [flip, setFlip] = useState(false)

    useEffect(async () => {
        const getData = async () => {
            const apiHero = await getApiHero(heroId)
            console.log(apiHero)
            setHero(apiHero)
        }
        getData()
    }, [heroId])
    return (
        <div
            style={{
                display: "flex",
                flexDirection: 'column',
                alignItems: 'center',
                justifyContent: 'center',
            }}
        >
            <div onClick={() => setFlip(!flip)}>
                {hero && <DfkHeroCard hero={hero} isFlipped={flip} isAnimated={true} />}
            </div>
            <div>
                <h1>Type Hero ID to display</h1></div>
            <div> <input
                    value={heroId}
                    onChange={(e) => setHeroId(e.target.value)}
                    style={{ width: 200 }}
                />
            </div>
            <div><br /> {hero && <DfkHeroSprite hero={hero} />}
            </div>
            <div><h2>Hero Stats</h2> <br />
                {hero && <HeroStatsBox hero={hero} />}
            </div>
        </div>
    )
}

export default App