import React from 'react'
import { calculateRemainingStamina } from '../../utils/staminaCalculations'
import HeroInfo from '../../components/HeroInfo'


const HeroStatBox = ({ hero }) => {
    console.log(hero)

    return (
        <div style={{
            backgroundImage: 'url(' + require('./scroll-horizontal-light.png') +')',
            backgroundPosition: 'center',
            backgroundSize: 'cover',
            backgroundRepeat: 'no-repeat',
            alignItems: 'center',
        }}>
            {hero.name}'s strength is {hero.stats.strength}
            <br />
            {hero.visualGenes.gender === 'female' ? 'She' : 'He'} is a {hero.statGenes.class} and a {hero.statGenes.subClass}<br />
            {hero.visualGenes.gender === 'female' ? 'She' : 'He'} currently has {calculateRemainingStamina(hero)} stamina out of {hero.stats.stamina} max.
        </div>
    )
}


export default HeroStatBox